@keyframes logo-shift {
  0%, 5%, 42%, 48%, 100% {
    transform: translate3d(0, 0, 0); }
  10% {
    transform: translate3d(-1px, -3px, 0); }
  20% {
    transform: translate3d(-5px, 0, 0); }
  30% {
    transform: translate3d(0, 3px, 0); }
  40% {
    transform: translate3d(1px, -1px, 0); }
  50% {
    transform: translate3d(-1px, 3px, 0); }
  60% {
    transform: translate3d(-5px, 1px, 0); }
  70% {
    transform: translate3d(3px, 1px, 0); }
  80% {
    transform: translate3d(-1px, -1px, 0); }
  90% {
    transform: translate3d(3px, 3px, 0); }
  95% {
    transform: translate3d(1px, -3px, 0); } }

.oddbooks-logo {
  width: 100%; }
  .oddbooks-logo .layer-1,
  .oddbooks-logo .layer-2 {
    animation: logo-shift linear infinite alternate both;
    mix-blend-mode: multiply; }
  .oddbooks-logo .layer-1 {
    animation-delay: 13.2ms;
    animation-duration: 93s;
    fill: #cb8129; }
  .oddbooks-logo .layer-2 {
    animation-delay: 134ms;
    animation-direction: alternate-reverse;
    animation-duration: 108s;
    fill: #b32356; }

@keyframes blur {
  0%, 60%, 75%, 80%, 100% {
    filter: blur(0); }
  70% {
    filter: blur(1.5px); }
  95% {
    filter: blur(3px); } }

@keyframes shake {
  0%, 5%, 42%, 48%, 100% {
    transform: translate3d(0, 0, 0) scale(1); }
  10% {
    transform: translate3d(-1px, -3px, 0) scale(1.006); }
  20% {
    transform: translate3d(-5px, 0, 0) scale(1); }
  30% {
    transform: translate3d(0, 3px, 0) scale(0.994); }
  40% {
    transform: translate3d(1px, -1px, 0) scale(0.994); }
  50% {
    transform: translate3d(-1px, 3px, 0) scale(1); }
  60% {
    transform: translate3d(-5px, 1px, 0) scale(1.006); }
  70% {
    transform: translate3d(3px, 1px, 0) scale(1.01); }
  80% {
    transform: translate3d(-1px, -1px, 0) scale(1.01); }
  90% {
    transform: translate3d(3px, 3px, 0) scale(1); }
  100% {
    transform: translate3d(1px, -3px, 0) scale(1); } }

@keyframes glitch {
  0% {
    opacity: 1;
    transform: skewX(0deg); }
  20% {
    transform: skewX(0deg); }
  21% {
    transform: skewX(-3deg); }
  35% {
    opacity: .5;
    transform: skewX(0deg); }
  35.5% {
    opacity: .25;
    transform: skewX(70deg); }
  36% {
    transform: skewX(-60deg); }
  36.5% {
    opacity: .5;
    transform: skewX(0deg); }
  100% {
    opacity: 1;
    transform: skewX(0deg); } }

